import { Component, OnInit, Output, EventEmitter, ViewEncapsulation } from '@angular/core';

import { Observable, BehaviorSubject, Subscription, Subject } from 'rxjs';
import { filter } from 'rxjs/operators';
import { FormControl } from '@angular/forms';

import { ModalsService } from 'src/app/modules/ui/modals/modals.service';

@Component({
  selector: 'enquiry-mode',
  templateUrl: './enquiry-mode.component.html',
  styleUrls: ['./enquiry-mode.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class EnquiryModeComponent implements OnInit {

  constructor(
    private modalsService: ModalsService
  ) {

    // Set the private defaults
    this._unsubscribeAll = new Subject();

    this._modesList = new BehaviorSubject(null);
    this.searchData = new FormControl('');

   }

  isExpanded:boolean = false;

  // Private
  private _unsubscribeAll: Subject<any>;

  isDataLoaded:boolean = true;

  programData:string = "";

  _modesList:any;
  fullModesList:any = [];

  searchData:FormControl;

  search:string = '';
  deleteSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 5;

  allData: any;

  @Output() newEnquirySetupCategoryEvent = new EventEmitter<boolean>();
  @Output() editEnquirySetupCategoryEvent = new EventEmitter<any>();
  @Output() viewEnquirySetupCategoryEvent = new EventEmitter<any>();

  get modes$(): Observable<any>{
    return this._modesList.asObservable();
  }

  addEnquiryMode(event, data) {
    event.stopPropagation();
    this.newEnquirySetupCategoryEvent.next(data)
  }

  editEnquiryMode(data) {
    data.category = 'mode'
    this.editEnquirySetupCategoryEvent.next(data)
  }

  viewEnquiryMode(data) {
    data.category = 'mode'
    this.viewEnquirySetupCategoryEvent.next(data)
  }

  deleteEnquiryMode(data) {
    this.modalsService.showDeleteConfirmModal(data.id)
  }

  ngOnInit(): void {

    // Load modes
    let res = [
      {
        "id": 107,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "SMS",
        "description": "some SMS related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      },
      {
        "id": 108,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "Phone Call",
        "description": "some Phone Call related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      },
      {
        "id": 108,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "Email",
        "description": "some email related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      },
      {
        "id": 108,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "Direct",
        "description": "some direct related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      }
    ];

    this.fullModesList = res;
    this.allData = res;
    this._modesList.next(res);

    this.modes$.pipe( filter(res => res != null),
    ).subscribe((res:any) => {

      this.totalItems = res.length;
      this.ItemStartIndex = 0;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

    });

    this.deleteSubscribe = this.modalsService.deleteindexcast.subscribe((id:any) => {


      if(id != null) {

        this.fullModesList = this.fullModesList.filter(item => {
          return item.id != id
        })
        this._modesList.next(this.fullModesList);

      }

    })

  }

  ngOnDestroy(): void
    {
        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
        this.deleteSubscribe.unsubscribe();
    }

}
