import { Component, OnInit, ViewEncapsulation, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Subject } from 'rxjs';

import { MatDrawerToggleResult } from '@angular/material/sidenav';
import { EnquirySetupComponent } from 'src/app/modules/admin/enquiries/enquiry-setup/enquiry-setup.component';

import { EnquirySetupService } from 'src/app/modules/admin/enquiries/enquiry-setup/enquiry-setup.service';
import { SharedService } from 'src/app/shared/services/shared.service';
@Component({
  selector: 'app-add-enquiry-setup-category',
  templateUrl: './add-enquiry-setup-category.component.html',
  styleUrls: ['./add-enquiry-setup-category.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class AddEnquirySetupCategoryComponent implements OnInit {

  isEnquirySubmitted:boolean = true;
  isEdit:boolean = false;

  enquiry:any;
  category:string = '';

  private _unsubscribeAll: Subject<any>;

  isView:boolean = false;

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private _changeDetectorRef: ChangeDetectorRef,
    private _enquirySetupComponent: EnquirySetupComponent,
    private enquirySetupService: EnquirySetupService,
    private sharedService:SharedService
  ) {

     // Set the private defaults
     this._unsubscribeAll = new Subject();

   }

   closeDrawer(): Promise<MatDrawerToggleResult>{
    return this._enquirySetupComponent.matDrawer.close();
   }

  goBack() {
    this._enquirySetupComponent.matDrawer.close();
    this.enquirySetupService.setEnquirySetupMatDrawer(null);
    this._router.navigate(['.'], {relativeTo: this._activatedRoute.parent});
  }

  submitAddEnquiryTypeForm(form) {

  }

  ngOnInit(): void {

    this.enquiry = {

    }

    //Open the drawer
    this.enquirySetupService.enquirysetupdrawercast.subscribe(res => {

      if(res != null) {

        if(res.id != undefined && res.id == this._activatedRoute.params['value'].id){

          this.category = this._activatedRoute.params['value'].category
          this._enquirySetupComponent.matDrawer.open();
          this.enquiry = res.data

          if(this._activatedRoute.params['value'].type == 'edit'){
            this.isEdit = true
          }
          else if(this._activatedRoute.params['value'].type == 'view'){
            this.isView = true
          }
          else {
            this.enquiry = {}
          }
        }


      }

      //Mark for check
      this._changeDetectorRef.markForCheck();

    })

    if(this._activatedRoute.params['value'].type != 'new') {

      this.isEnquirySubmitted = true;
      this.isEdit = true;

    }

  }

}
