import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { EnquirySetupService } from 'src/app/modules/admin/enquiries/enquiry-setup/enquiry-setup.service';
import { SharedService } from 'src/app/shared/services/shared.service';


@Injectable({
  providedIn: 'root'
})
export class EnquirySetupResolver implements Resolve<boolean> {

  constructor(
    private router: Router,
    private enquirySetupService: EnquirySetupService,
    private sharedService:SharedService
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
  {

    var data = this.enquirySetupService.getEnquirySetupMatDrawer();

    if(data == null){

      const parentUrl = state.url.split('/').slice(0, -3).join('/');

      // Navigate to parent url
      this.router.navigateByUrl(parentUrl);

    }


    return;

  }

}
