import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { EnquirySetupComponent } from './enquiry-setup.component';
import { AddEnquirySetupCategoryComponent } from './add-enquiry-setup-category/add-enquiry-setup-category.component';
import { EnquirySetupResolver } from './enquiry-setup.resolver';

const routes: Routes = [
  { path: '', component: EnquirySetupComponent, canActivate: [AuthGuard],
    children: [
      {
        path         : ':category/:type/:id',
        component    : AddEnquirySetupCategoryComponent,
        resolve: {
          EnquirySetupResolver
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EnquirySetupRoutingModule { }
