import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class EnquirySetupService {

  private enquirySetupMatDrawer = new BehaviorSubject<any>(null);
  enquirysetupdrawercast = this.enquirySetupMatDrawer.asObservable();

  constructor() { }

  getEnquirySetupMatDrawer(){
    return this.enquirySetupMatDrawer.value
  }

  setEnquirySetupMatDrawer(value){
    this.enquirySetupMatDrawer.next(value);
  }

}
