import { Component, OnInit, ViewEncapsulation, ViewChild, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { Subject } from 'rxjs';

import { MatDrawer } from '@angular/material/sidenav';

import { EnquirySetupService } from './enquiry-setup.service';
import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'enquiry-setup',
  templateUrl: './enquiry-setup.component.html',
  styleUrls: ['./enquiry-setup.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class EnquirySetupComponent implements OnInit {


  isDataLoaded:boolean = true;

  @ViewChild('matDrawer', {static: true})
  matDrawer: MatDrawer;

  drawerMode: 'over';
  private _unsubscribeAll: Subject<any>;


  constructor(
    private _changeDetectorRef: ChangeDetectorRef,
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private enquirySetupService: EnquirySetupService,
    private sharedService:SharedService
  ) {

    this._unsubscribeAll = new Subject();

  }

  onBackdropClicked() {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    // Go to the parent route
    this._router.navigate(['/enquiries/setup'], {relativeTo: route});
    this.matDrawer.close();

    // Mark for check
    this._changeDetectorRef.markForCheck();

  }

  addEnquirySetupCategory(data) {

    console.log(data)

    if(data) {

      // Get the current activated route
      let route = this._activatedRoute;
      while ( route.firstChild )
      {
          route = route.firstChild;
      }

      var id = this.sharedService.guid();

      this.enquirySetupService.setEnquirySetupMatDrawer({id:id, data: null, isedit: false});
      this._router.navigate([ data, 'new' , id], {relativeTo: route});

      //Mark for check
      this._changeDetectorRef.markForCheck();

    }

  }

  editEnquirySetupCategory(data) {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    this.enquirySetupService.setEnquirySetupMatDrawer({id:data.id, data: data, isedit: true});
    this._router.navigate([data.category, 'edit' , data.id], {relativeTo: route});

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  viewEnquirySetupCategory(data) {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    this.enquirySetupService.setEnquirySetupMatDrawer({id:data.id, data: data, isedit: false});
    this._router.navigate([data.category, 'view' , data.id], {relativeTo: route});

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  addEnquiryMode(event) {

    if(event) {

      // Get the current activated route
      let route = this._activatedRoute;
      while ( route.firstChild )
      {
          route = route.firstChild;
      }

      var id = this.sharedService.guid();

      this.sharedService.setMatDrawerId(id);
      this._router.navigate(['mode/new', id], {relativeTo: route});

      //Mark for check
      this._changeDetectorRef.markForCheck();

    }

  }

  editEnquiryMode(id) {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    this.sharedService.setMatDrawerId(id);
    this._router.navigate(['mode/edit', id], {relativeTo: route});

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  addEnquirySource(event) {

    if(event) {

      // Get the current activated route
      let route = this._activatedRoute;
      while ( route.firstChild )
      {
          route = route.firstChild;
      }

      var id = this.sharedService.guid();

      this.sharedService.setMatDrawerId(id);
      this._router.navigate(['source/new', id], {relativeTo: route});

      //Mark for check
      this._changeDetectorRef.markForCheck();

    }

  }

  editEnquirySource(id) {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    this.sharedService.setMatDrawerId(id);
    this._router.navigate(['source/edit', id], {relativeTo: route});

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  onGlSearchFilter(event) {

  }

  ngOnInit(): void {


  }

}
