import { Component, OnInit, Output, EventEmitter, ViewEncapsulation } from '@angular/core';

import { Observable, BehaviorSubject, Subscription, Subject } from 'rxjs';
import { filter } from 'rxjs/operators';
import { FormControl } from '@angular/forms';

import { ModalsService } from 'src/app/modules/ui/modals/modals.service';


@Component({
  selector: 'enquiry-type',
  templateUrl: './enquiry-type.component.html',
  styleUrls: ['./enquiry-type.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class EnquiryTypeComponent implements OnInit {

  isExpanded:boolean = false;

  // Private
  private _unsubscribeAll: Subject<any>;

  isDataLoaded:boolean = true;

  programData:string = "";

  _typesList:any;
  fullTypesList:any = [];

  searchData:FormControl;

  search:string = '';
  deleteSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 5;

  allData: any;

  @Output() newEnquirySetupCategoryEvent = new EventEmitter<boolean>();
  @Output() editEnquirySetupCategoryEvent = new EventEmitter<any>();
  @Output() viewEnquirySetupCategoryEvent = new EventEmitter<any>();

  constructor(
    private modalsService: ModalsService
  ) {

    // Set the private defaults
    this._unsubscribeAll = new Subject();

    this._typesList = new BehaviorSubject(null);
    this.searchData = new FormControl('');

   }

  get types$(): Observable<any>{
    return this._typesList.asObservable();
  }


  addEnquiryType(event, data) {
    event.stopPropagation();
    this.newEnquirySetupCategoryEvent.next(data)
  }

  editEnquiryType(data) {
    data.category = 'type'
    this.editEnquirySetupCategoryEvent.next(data)
  }

  viewEnquiryType(data) {
    data.category = 'type'
    this.viewEnquirySetupCategoryEvent.next(data)
  }

  deleteEnquiryType(data) {
    this.modalsService.showDeleteConfirmModal(data.id)
  }

  ngOnInit(): void {

    // Load types
    let res = [
      {
        "id": 107,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "General",
        "description": "some general related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      },
      {
        "id": 108,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "Booking",
        "description": "some booking related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      }

    ];

    this.fullTypesList = res;
    this.allData = res;
    this._typesList.next(res);

    this.types$.pipe( filter(res => res != null),
    ).subscribe((res:any) => {

      this.totalItems = res.length;
      this.ItemStartIndex = 0;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

    });

    this.deleteSubscribe = this.modalsService.deleteindexcast.subscribe((id:any) => {


      if(id != null) {

        this.fullTypesList = this.fullTypesList.filter(item => {
          return item.id != id
        })
        this._typesList.next(this.fullTypesList);

      }

    })

  }

  ngOnDestroy(): void
    {
        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
        this.deleteSubscribe.unsubscribe();
    }

}
